



























































































































































































































































































































import Vue from "vue";
export default Vue.extend({
  name: "QMenu",
  data: () => ({
    mini: true,
    resultList: [Object],
    innerClass: "",
    containes: false,
  }),
  props: {
    activeColor: {
      type: String,
    },
    width: {
      type: String,
    },
    miniVariantWidth: {
      type: String,
    },
    menuData: {
      type: [Object],
    },
    collapseOnItemSelect: {
      type: Boolean,
      default: false,
    },
    hideMenuTitle: {
      type: Boolean,
      default: false,
    },
    fixed: {
      type: Boolean,
    },
    color: {
      type: String,
    },
    autoCollapse: {
      type: Boolean,
    },
    title: {
      type: String,
    },
    showOnlyIcon: {
      type: Boolean,
      default: false,
    },
    expandOnHover: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    menuData: {
      immediate: true,
      handler(newValue) {
        this.resultList = newValue ?? [];
        this.childrenSetActiveDefaultAsFalse(this.resultList);
      },
    },
    color: {
      immediate: true,
      handler(newValue) {
        this.setTheme();
      },
    },
    mini: {
      immediate: true,
      handler(newValue) {
        this.$emit("update:mini-variant", newValue);
      },
    },
  },
  mounted() {},
  destroyed() {
    this.$el?.remove();
  },
  methods: {
    setTheme() {
      switch (this.activeColor) {
        case "primary":
          return "primaryTheme";
        case "secondary":
          return "secondaryTheme";
        case "success":
          return "successTheme";
        case "error":
          return "errorTheme";
        case "warning":
          return "warningTheme";
        default:
          if (this.color == undefined) {
            return "secondaryTheme";
          } else {
            return "white--text";
          }
      }
    },

    isIconPathAvaliable(imagePath: any) {
      let isAvaliable =
        imagePath && imagePath.startsWith("mdi-") ? true : false;
      return isAvaliable;
    },
    isImagePathAvaliable(imagePath: any) {
      let isAvaliable =
        imagePath &&
        (imagePath.toLocaleLowerCase("tr").startsWith("http:") ||
          imagePath.toLocaleLowerCase("tr").startsWith("https:"))
          ? true
          : false;
      return isAvaliable;
    },
    setPrependIcon(imagePath: any) {
      return imagePath && imagePath.startsWith("mdi-") ? imagePath : "";
    },
    setIconSize(iconPath: any) {
      return {
        size: iconPath?.iconOptions?.size,
        "x-small": !!iconPath?.iconOptions?.xSmall,
        small: !!iconPath?.iconOptions?.small,
        large: !!iconPath?.iconOptions?.large,
        "x-large": !!iconPath?.iconOptions?.xLarge,
        color:iconPath?.iconOptions?.color
      };
    },
    setImageSize(imagePath: any) {
      return {
        height: imagePath?.imageOptions?.height,
        width: imagePath?.imageOptions?.width,
      };
    },
    childrenSetActiveDefaultAsFalse(list) {
      list.forEach((item: Object) => {
        if (
          Object(item).imagePath &&
          !Object(item).imagePath.startsWith("mdi-")
        ) {
          if (
            !Object(item)
              .imagePath.toLocaleLowerCase("tr")
              .startsWith("http:") &&
            !Object(item).imagePath.toLocaleLowerCase("tr").startsWith("https:")
          ) {
            let protocolAndPath =
              window.location.protocol + "//" + window.location.host;
            this.$set(
              item,
              "imagePath",
              protocolAndPath +
                (Object(item).imagePath.startsWith("/")
                  ? Object(item).imagePath
                  : "/" + Object(item).imagePath)
            );
          }
        }
        if (!Object(item).active) {
          this.$set(item, "active", false);
        }
        if (Object(item).children) {
          this.childrenSetActiveDefaultAsFalse(Object(item).children);
        }
      });
    },
    menuItemsLoop(list, clickedItem) {
      this.containes = false;
      list.forEach((item: any) => {
        if (!Object(item).children) {
          if (Object(item).active) {
            setTimeout(function () {
              Object(item).active = false;
            }, 100);
          }
        } else {
          if (this.autoCollapse) {
            this.isCurrentGroupContainsClickedItem(item, clickedItem);
            if (this.containes == false) {
              Object(item).active = false;
            }
          }
          this.menuItemsLoop(Object(item).children, clickedItem);
        }
      });
    },

    isCurrentGroupContainsClickedItem(item, clickedItem) {
      item.children.forEach((child) => {
        if (child == clickedItem) {
          this.containes = true;
        } else {
          if (child.children) {
            this.isCurrentGroupContainsClickedItem(child, clickedItem);
          }
        }
      });
    },
    clickRow(item: Object, index: Number) {
      if (this.collapseOnItemSelect) {
        this.updateMini();
      }
      if (Object(item).active) {
        Object(item).active = false;
        this.$emit("click:row", item);
      } else {
        const self = this;
        this.menuItemsLoop(this.resultList, item);
        setTimeout(function () {
          Object(item).active = true;
          self.$emit("click:row", item);
        }, 100);
      }
    },
    calculatePadding(index: number, item: any): Number {
      let padding = 0;

      if (item.imagePath && item.imagePath.startsWith("mdi-")) {
        padding = 15;
      } else if (
        item.imagePath &&
        (item.imagePath.toLocaleLowerCase("tr").startsWith("http:") ||
          item.imagePath.toLocaleLowerCase("tr").startsWith("https:"))
      ) {
        padding = 15;
      } else {
        padding = 15 * index + 24 * index;
      }
      return padding;
    },
    collapseAll() {
      this.resultList.forEach((item: Object) => {
        Object(item).active = false;
      });
    },
    updateMini() {
      if (this.expandOnHover) {
        return;
      }
      this.collapseAll();
      this.mini = !this.mini;
    },
    onClickGroup() {
      if (this.mini) {
        this.mini = false;
      }
      if (this.autoCollapse) {
        this.resultList.forEach((item: Object) => {
          if (Object(item).active) {
            setTimeout(function () {
              Object(item).active = false;
            }, 100);
          }
        });
      }
    },
  },
});
